async function bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    let params = {};
    params["moduleName"] = modulename; //"CRM";
    params["type"] = filtername
   await CampaignDataServices.GetLeftSearchStatus(params)
        .then(function (response) {
        var json = response.data;
            if (json != null) {
                if (filtername == "CAMPAIGN_OWNER_SEARCH") {
                    vm.leftSearchFields[1].listOptions = json.DATA;
                    vm.LeadStatusList = json.DATA;
                    return vm.LeadStatusList;
                }
            }
        });
}
export default { buttons,listActions, leftsearchSchema,bindleftCommonSearchdropdown};